.banner {
  position: relative;
  height: 448px;
  color: white;
  object-fit: contain;
}

.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner__description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}

.banner__fadeBottom {
  height: 7.6rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(64, 0, 0, 0.553),
    #000000
  );
}

.banner__button {
  cursor: pointer;
  color: white;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(92, 1, 1, 0.867);
  padding-bottom: 0.5rem;
}

.banner__button:hover {
  color: rgb(39, 0, 0);
  background-color: rgb(255, 255, 255);
  transition: all 0.7s;
}
